import React from 'react'

export interface HeaderContextProp {
    headerVisible: boolean;
    setHeaderVisible: (value: boolean) => void;
}

const HeaderContext = React.createContext<HeaderContextProp>({ headerVisible: true, setHeaderVisible: () => { } });

export const HeaderProvider = HeaderContext.Provider
export const HeaderConsumer = HeaderContext.Consumer

export default HeaderContext

