import React, { useState } from "react";
import { connect } from "formik";
import * as Yup from "yup"
import FormikContainer from "../../components/formikContainer/FormikContainer";
import NewExpense from "./componets/NewExpense";
import { Expense } from "../../models/Expense";


export const CreateNewExpense: React.SFC<{}> = () => {

    const defaulValue: Expense = { urlImage: "", amount: undefined, location: { latitude: 0, longitude: 0 }, dateBoy: new Date() }

    const [expense, setExpense] = useState<Expense>(defaulValue)

    const validationSchema = Yup.object({
        email: Yup.string()
            .required("Name is required, sqn")
    })



    return (
        <FormikContainer
            validationSchema={validationSchema}
            initialValues={{ ...expense, locationDesciption: "" }}
            onSubmit={() => { }}
        >
            <NewExpense />

        </FormikContainer >
    );
}

export default connect(CreateNewExpense);