import * as React from 'react';
import CreateNewExpense from '../view/createNewExpense/CreateNewExpense';
import { Sidebar } from '../components/sidebar/Sidebar';
import DashBoard from '../view/dashBoard/DashBoard';
import { HeaderProvider } from './HeaderContext';
import { useState } from 'react';
import { Switch, Route } from 'react-router';
import { BrowserRouter } from 'react-router-dom';


export const Home: React.SFC<{}> = () => {

    const [headerVisible, setHeaderVisible] = useState<boolean>(true);

    return (
        <>
            <BrowserRouter >
                <Switch >
                    <HeaderProvider value={{ headerVisible: headerVisible, setHeaderVisible: setHeaderVisible }}>
                        <Sidebar >
        
                            <Route path="/CreateNewExpense" component={CreateNewExpense} />
                            <Route path="/DashBoard" component={DashBoard} />
                            <Route path="/" exact component={DashBoard} />

                        </Sidebar>
                    </HeaderProvider>
                </Switch>
            </BrowserRouter>
        </>
    );
}